$black-rgb: 0,0,0;
$body-color-rgb: rgb(33,37,41);
$body-bg-rgb: rgb(255,255,255);
$font-sans-serif: system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue","Noto Sans","Liberation Sans",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
$font-monospace: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
$gradient: linear-gradient(180deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0));

//fonts
$body-font-family: var(--bs-font-sans-serif);
$body-font-size: 1rem;
$body-font-weight: 400;
$body-line-height: 1.5;
$body-color: #212529;

$emphasis-color: #000;
$emphasis-color-rgb: 0,0,0;
$secondary-color: rgba(33, 37, 41, 0.75);
$secondary-color-rgb: 33,37,41;
$secondary-bg: #e9ecef;
$secondary-bg-rgb: 233,236,239;
$tertiary-color: rgba(33, 37, 41, 0.5);
$tertiary-color-rgb: 33,37,41;
$tertiary-bg: #f8f9fa;
$tertiary-bg-rgb: 248,249,250;
$body-bg: #fff;
$body-bg-rgb: 255,255,255;
$link-color: #0d6efd;
$link-color-rgb: 13,110,253;
$link-decoration: underline;
$link-hover-color: #0a58ca;
$link-hover-color-rgb: 10,88,202;
$code-color: #d63384;
$highlight-bg: #fff3cd;

//border-style
$border-width: 1px;
$border-style: solid;
$border-color: #dee2e6;
$border-color-translucent: rgba(0, 0, 0, 0.175);

//border-radius
$border-radius: 0.375rem;
$border-radius-sm: 0.25rem;
$border-radius-lg: 0.5rem;
$border-radius-xl: 1rem;
$border-radius-2xl: 2rem;
$border-radius-pill: 50rem;

//box-shadow
$box-shadow: 0 0.5rem 1rem rgba($body-color-rgb, 0.15);
$box-shadow-sm: 0 0.125rem 0.25rem rgba($body-color-rgb, 0.075);
$box-shadow-lg: 0 1rem 3rem rgba($body-color-rgb, 0.175);
$box-shadow-inset: inset 0 1px 2px rgba($body-color-rgb, 0.075);
$emphasis-color: #000;
$form-control-bg: var(--bs-body-bg);
$form-control-disabled-bg: var(--bs-secondary-bg);
$highlight-bg: #fff3cd;
$breakpoint-xs: 0;
$breakpoint-sm: 576px;
$breakpoint-md: 768px;
$breakpoint-lg: 992px;
$breakpoint-xl: 1200px;
$breakpoint-xxl: 1400px;