@import '../../common/scss/variables';

.mainContainer {
  display: flex;
  flex-direction: column;
  justify-content: end;
  background: #fff;
  box-shadow: $box-shadow-sm;
  border-radius: $border-radius-xl;
  margin-bottom: 2rem;
  margin-top: 1rem;
  padding: 2rem;

  .headerMessage {
    background: #66cdff;
    height: 4rem;
    margin-bottom: 2rem;
    border-radius: 11px;
    position: relative;

    .arrowBack {
      color: #fff;
      margin-left: 1rem;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
    }

    .avatar {
      width: 3rem;
      border-radius: 50%;
      margin: .5rem 0 .5rem 3rem;
    }
  }

  .dialog {
    display: flex;
    flex-direction: column;
    align-items: end;
    width: 100%;

    .sendFormContainer {
      width: 100%;
      position: relative;

      .textarea {
        width: 100%;
        border-radius: 11px;
        box-shadow: $box-shadow-sm;
        resize: none;
        padding: 1rem;
        outline: none;
        margin-top: 1rem
      }

      .button {
        position: absolute;
        bottom: 50%;
        right: 1rem;
        transform: translateY(50%);
        background: #fff;
        border: none;
        color: $purple;
        cursor: pointer;
      }
    }
  }
}


