.loader_background {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  z-index: 1001;

  width: 120px;
  height: 120px;
  margin-top: 20%;

  border: 20px solid #f3f3f3;
  border-top: 20px solid #366eff;
  border-radius: 50%;

  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
